<template>
  <tas-base-crud ref="sub-module" :config="config" hide-update hide-filter hide-detail>
    <template v-slot:list-table-action="data">
      <a @click="openModal('modal-rincian-beasiswa', data)" class="btn btn-info btn-sm">Lihat Detail</a>
    </template>
    <template v-slot:list-header>
      <b-modal header-class="pb-1" v-if="modalData.detail" id="modal-rincian-beasiswa" hide-footer size="xl">
        <template #modal-header>
          <div class="d-flex justify-content-between flex-fill">
            <div class="d-flex flex-column flex-fill">
              <div class="text-center mb-5">
                <h5 class="mb-1 font-weight-bold">Detail {{ config.title }}</h5>
                <span class="font-weight-bolder">Periode {{ modalData.detail.periode_month | parse('period') }}</span>
              </div>
              <table class="table no-border mb-0">
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">Tenaga Kerja</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.rel_employee_id }}</td>
                  <td class=""></td>
                  <td class="pt-0 pl-0 nowrap-table">Jabatan</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.rel_job_position_id }}</td>
                </tr>
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">MPP</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.is_mpp | parse('ynBool') }}</td>
                  <td class=""></td>
                  <td class="pt-0 pl-0 nowrap-table">Status Ketenagakerjaan</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.rel_status_employment_id }}</td>
                </tr>
              </table>
            </div>
          </div>
        </template>
        <div class="tables-responsive">
          <table class="table table-header-custom v-center">
            <thead>
              <tr>
                <th>Sanksi Tenaga Kerja</th>
                <th class="nowrap-table">Sudah Dibayarkan</th>
                <th class="nowrap-table">Nilai Dasar Perbulan</th>
                <th class="nowrap-table">Sisa Nilai Sanksi</th>
                <th>Pembayaran Bulan ini</th>
              </tr>
            </thead>
            <tbody>
              <template v-if="modalData.detail.data_potongan_sanksi_components.length">
                <tr v-for="(e,i) in modalData.detail.data_potongan_sanksi_components" :key="i+'-data-employee'">
                  <td>
                    <label class="checkbox">
                      <input @change="assignValue(e)" v-model="e.active" type="checkbox" :true-value="1" :false-value="0">
                      <span class="mr-3"></span>
                      <div>
                        <span>{{ e.rel_sanksi_type_id }}</span>
                        <div class="text-line-2 max-w-200px font-size-sm">{{ e.sanksi_description }}</div>
                        <div class="d-flex justify-content-between">
                          <span class="">Rp.</span>
                          <span class="text-right font-weight-bold">{{ e.sanksi_value | parse('number') }}</span>
                        </div>
                      </div>
                    </label>
                  </td>
                  <td>{{ e.count_payment | parse('number') }} Kali</td>
                  <td>
                    <div class="d-flex justify-content-between">
                      <span class="">Rp.</span>
                      <span class="text-right font-weight-bold">{{ e.base_payment_value | parse('number') }}</span>
                    </div>
                  </td>
                  <td>
                    <div class="d-flex justify-content-between">
                      <span class="">Rp.</span>
                      <span class="text-right font-weight-bold">{{ e.remaining_sanksi_value | parse('number') }}</span>
                    </div>
                  </td>
                  <template v-if="e.active">
                    <td class="bg-light-primary" style="width:25%">
                      <s-input v-model="e.component_value" :item="{ type:'number', validation:[], label:null, prefix: 'Rp. ', groupClass:'mb-0', ic:'w-100 mr-1', api:'', attr: { precision:2 } }" />
                    </td>
                  </template>
                  <td v-else class="nowrap-table" style="width:25%">
                    <div class="d-flex justify-content-between">
                      <span class="">Rp.</span>
                      <span class="text-right font-weight-bold">{{ e.component_value | parse('number') }}</span>
                    </div>
                  </td>
                </tr>
              </template>
              <tr v-else>
                <td colspan="5" class="text-center">
                  <p class="mb-0">Tidak ada Sanksi yang terdaftar</p>
                </td>
              </tr>
              <tr>
                <th colspan="4" class="text-right text-uppercase">Jumlah Pembayaran</th>
                <th>
                  <div class="d-flex justify-content-between">
                    <span class="">Rp.</span>
                    <span class="text-right font-weight-bolder">{{ modalData.detail.potongan_value || 0 | parse('number') }}</span>
                  </div>
                </th>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="mt-5">
          <button @click="submitData('modal-rincian-bills')" class="btn btn-primary btn-lg btn-block font-size-md">Perbarui &amp; Simpan</button>
        </div>
      </b-modal>
    </template>
  </tas-base-crud>
</template>

<script>
export default {
  name: 'crud-potongan-sanksi-details',
  data () {
    return {
      modalData: {},
      mainKey: 0,
      config: {
        title: 'Data Sanksi Tenaga Kerja',
        model_api: null,
        getter: 'custom/potongan-sanksi-details',
        setter: 'potongan-sanksi-details',
        pk_field: 'name',
        filter_api: {},
        custom_api: {
          create: 'custom/potongan-sanksi-details'
        },
        permission: {
          create: 'create-potongan-sanksi-details',
          read: 'view-potongan-sanksi-details',
          update: 'update-potongan-sanksi-details',
          delete: 'delete-potongan-sanksi-details'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          {
            id: 'status_code',
            data: 'status_code',
            label: 'Status',
            placeholder: null,
            methods: {
              list: {
                order: true,
                class: { 'not_calculated_yet': 'badge badge-danger', 'calculated': 'badge badge-primary' },
                transform: 'calculated'
              },
              detail: {
                order: true,
                class: { 'not_calculated_yet': 'badge badge-danger', 'calculated': 'badge badge-primary' },
                transform: 'calculated'
              },
              create: false,
              update: {
                view_data: 'status_code',
                type: 'radio',
                value: 'calculated',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Sudah dihitung', code: 'calculated' }, { label: 'Belum dihitung', code: 'not_calculated_yet' }]
                  }
                }
              },
              filter: {
                view_data: 'status_code',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Sudah dihitung', code: 'calculated' }, { label: 'Belum dihitung', code: 'not_calculated_yet' }]
                  }
                }
              }
            }
          },
          {
            id: 'employee_id',
            label: 'Nama Tenaga Kerja',
            methods: {
              list: { view_data: 'rel_employee_id' },
              create: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              update: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              filter: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              }
            }
          },
          {
            id: 'job_position_id',
            label: 'Jabatan',
            methods: {
              list: { view_data: 'rel_job_position_id' },
              create: false,
              update: {
                setter: 'job-positions',
                getter: 'job-positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              },
              filter: {
                setter: 'job-positions',
                getter: 'job-positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              }
            }
          },
          {
            id: 'status_employment_id',
            label: 'Status Ketenagakerjaan',
            methods: {
              list: { view_data: 'rel_status_employment_id', show: true },
              create: false,
              update: {
                setter: 'custom/master-status-employments',
                getter: 'custom/master-status-employments',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  },
                  get_param: [{ 'id': 'job_position_id' }]
                }
              },
              filter: {
                setter: 'custom/master-status-employments',
                getter: 'custom/master-status-employments',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  },
                  get_param: [{ 'id': 'job_position_id' }]
                }
              }
            }
          },
          { id: 'potongan_value', label: 'Potongan', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'description', label: 'Deskripsi', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } }
        ]
      }
    }
  },
  watch: {
    mainKey: {
      immediate: false,
      deep: false,
      handler (e) {
        this.$refs['sub-module'].$children[1].getData()
      }
    }
  },
  methods: {
    assignValue (e) {
      if (e.active) e.component_value = e.base_payment_value
      else e.component_value = 0
    },
    async openModal (id, data) {
      const detail = {
        detail: await this.getDetail('get', 'custom/potongan-sanksi-details/show', { id: data.rowData.id })
      }
      this.modalData = Object.assign({}, detail, data.rowData)
      this.$nextTick().then(e => {
        this.$bvModal.show(id)
      })
    },
    getDetail (method, api, param, obj = null) {
      return this.$_api[method](api, param).then(res => {
        return obj ? res.data[obj] || res.data : res.data
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    copyValue (obj = null) {
      if (!obj) {
        this.$set(this.modalData, 'detail', { ...this.modalData.detail, ...this.modalData.sync.data_recaps })
      }
    },
    getDatasets (api, param, obj = null) {
      return this.$_api.dataset(api, param).then(res => {
        return obj ? res.data[obj] || res.data : res.data
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    submitData () {
      const api = this.config.getter + '/calculate'
      this.$_api.post(api, this.modalData.detail).then(res => {
        this.$_alert.success(null, 'Detail Rincian berhasil diperbarui')
        this.$set(this.modalData, 'detail', res.data)
        this.mainKey++
      }).catch((err) => {
        this.$_alert.error(err)
      })
    }
  }
}
</script>



